import { useState, useEffect } from "react";
import { Container, Row, Col, Link } from "react-bootstrap";
import headerImg from "../assets/img/header-img.svg";
import headerTubo from "../assets/img/header-tubo.svg";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState('');
  const [delta, setDelta] = useState(300 - Math.random() * 100);
  const [index, setIndex] = useState(1);
  // const toRotate = [ "Web Developer", "Web Designer", "UI/UX Designer" ];
  const toRotate = [ "Data Analytics", "Data Warehouse", "Business Intelligence", "ajudar voc" ];
  // const period = 1000;
  const period = 500;

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, delta);

    return () => { clearInterval(ticker) };
  }, [text])

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta / 2);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex(prevIndex => prevIndex - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setIndex(1);
      setDelta(200);
      // setDelta(500);
    } else {
      setIndex(prevIndex => prevIndex + 1);
    }
  }

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="aligh-items-center">
          <Col xs={12} md={6} xl={7}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                <span className="tagline">Bem-vindo  DataLabs</span>
                <h1>{`Somos especialistas em`} <span className="txt-rotate" dataPeriod="500" data-rotate='[ "Data Analytics", "Data Warehouse", "Business Intelligence", "ajudar voc" ]'><br/><span className="wrap">{text}</span></span></h1>
                  <p>Nossa misso  ajudar empresas a tomar decises inteligentes e estratgicas atravs do uso de dados. Na era digital, informaes valiosas esto disponveis em abundncia, mas muitas empresas ainda lutam para transform-las em insights.  a que entramos em cena. </p>
                  <p>Nossa consultoria em data analytics e Business Intelligence (BI) oferece solues personalizadas para atender s necessidades de cada empresa.</p>
                  {/* <a href="#connect">Conectar-se <ArrowRightCircle size={25} /></a> */}
              </div>}
            </TrackVisibility>
          </Col>
          <Col xs={12} md={6} xl={5}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div style={{justifyContent: 'center', alignItems: 'center', position: "relative"}} className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                  <img src={headerTubo} style={{position: "absolute"}} alt="Header Img"/> 
                  <img src={headerImg} className="logoHeader" alt="Header Img"/>
                </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
